@import 'variables';



html {
  height: 100vh;
  line-height: 1.2; /* Высота строки для всех браузеров */
  -webkit-text-size-adjust: 100%; /* фикс размера шрифта в iOS при повороте экрана */
  -ms-text-size-adjust: 100%; /* фикс размера шрифта в ms при повороте экрана */
  -moz-text-size-adjust: 100%; /* фикс размера шрифта в mozilla при повороте экрана */
  -ms-overflow-style: scrollbar; /* стандартные полосы прокрутки */
}
img {
    margin: 0;
    padding: 0;
}

body {
    margin: 0;
    padding: 20px;
    height: 85vh;
//     min-height: 100%;
    font: 1.05rem/1.35 'Nunito', serif;
    background-color: $background-color;
    color: #4d4d4d;
    overflow-x: hidden;
}


h1, h2, h3, h4, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
}

.cover {
}

div .button {
    display: flex;
}

button, select {
    font-family: 'Nunito';
    text-transform: uppercase;
    letter-spacing: .7rem;
    border: none;
    margin: 0 10px 10px 10px;
    padding: 10px 0px;
    border-radius: 9px;
    background: $background-color;
    transition: 0.3s;
    min-width: $card-min-width;
  }

button:hover, select:hover {
    box-shadow: $box-shadow;
    transform: scale(1.05);
}

.active{
    font-weight: 700;
    color: black;
//     color: $head-menu-color;
    text-decoration: none;
}

.normal {
    color: $head-menu-color;
    text-decoration: none;
}

@media (max-width: 840px) {
    button, select {
        font-size: 2em;
    }
    div .button {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        min-width: $card-min-width;
    }
}
@media (min-width: 840px) {
    button, select {
        font-size: calc( (100vw - 10rem)/(80 - 30) * (1.5 - 0.8) + 0.8rem);
    }
    div .button {
        justify-content: center;
        align-items: center;
        flex-flow: row wrap;
    }
}

