@import 'variables';


.home {
//     min-height: 70vh;
    display: flex;
    justify-content: left;
    //   align-items: center;
    flex-flow: row wrap;
    //  //overflow: hidden;
}

.about {
    min-width: $card-min-width;
    margin: 0;
}

.name {
    font-size: calc( (100vw - 30rem)/(80 - 30) * (4 - 2) + 2rem);
    font-weight: 200;
    text-transform: uppercase;
    letter-spacing: .7rem;
    color: black;
    min-width: $card-min-width;
    margin: 20vw 0 0 20px;
}

.stack {
    font-size: 1rem;
    font-weight: 500;
    color: black;
    margin: 0 0 0 20px;
}


.foto_div {
    position: absolute;
    overflow: hidden;
}
.foto {
    width: 100%;
    object-fit: cover;
    opacity: 0.2;

}

@media (min-width: 840px) {
    .foto_div {
       height: calc(100vh - 150px);
    }
    .foto_div {
        top: 150px;
        left: 45%;
        width: 60vw;
        min-width: 690px;
    }
}
@media (max-width: 840px) {
    .foto_div {
        height: calc(100vh - 300px);
    }
    .foto_div {
        top: 300px;
        right: -260px;
        width: 105vw;
        min-width: 555px;
    }
}
